import { Component } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { Settings, Api } from '../../providers';
import { Storage } from '@ionic/storage';

/**
 * The Settings page is a simple form that syncs with a Settings provider
 * to enable the user to customize settings for the app.
 *
 */
@IonicPage()
@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html'
})
export class SettingsPage {
  // Our local settings object
  options: any;

  settingsReady = false;

  form: FormGroup;

  profileSettings = {
    page: 'profile',
    pageTitleKey: 'SETTINGS_PAGE_PROFILE'
  };

  page: string = 'main';
  pageTitleKey: string = 'SETTINGS_TITLE';
  pageTitle: string;

  subSettings: any = SettingsPage;
  pushid: any;

  constructor(public navCtrl: NavController,
    public settings: Settings,
    public formBuilder: FormBuilder,
    public navParams: NavParams,
    public translate: TranslateService,
    private storage: Storage,
    public api: Api) {
      this.storage.get('pushid')
      .then((pushid) => {
        if (pushid) {
          this.pushid = pushid;
        } else {
          this.pushid = 'undefined';
        }
    });

  }

  _buildForm() {
    let group: any = {
      language: [this.options.language]
    };

    switch (this.page) {
      case 'main':
        break;
      case 'profile':
        group = {
          option4: [this.options.option4]
        };
        break;
    }
    this.form = this.formBuilder.group(group);

    // Watch the form for changes, and
    this.form.valueChanges.subscribe((v) => {

      this.settings.merge(this.form.value);
      console.log(this.form.value);
      console.log(this.pushid);
      return this.updateLang(this.form.value);
    });
  }

  ionViewDidLoad() {
    // Build an empty form for the template to render
    this.form = this.formBuilder.group({});
  }

  ionViewWillEnter() {
    // Build an empty form for the template to render
    this.form = this.formBuilder.group({});

    this.page = this.navParams.get('page') || this.page;
    this.pageTitleKey = this.navParams.get('pageTitleKey') || this.pageTitleKey;

    this.translate.get(this.pageTitleKey).subscribe((res) => {
      this.pageTitle = res;
    })

    this.settings.load().then(() => {
      this.settingsReady = true;
      this.options = this.settings.allSettings;

      this._buildForm();
    });
  }

  ngOnChanges() {

  }

  /**
   * Capture the selected language from the  component
   *
   * @public
   * @method changeLanguage
   * @return {none}
   */
  public changeLanguage() : void
  {
     this._translateLanguage();
  }




  /**
   * Implement the selected language via the Translate service
   *
   * @private
   * @method _translateLanguage
   * @return {none}
   */
  private _translateLanguage() : void
  {
    this.translate.use(this.options.language);
  }

  updateLang(lang) {
    if( this.pushid !==null) {
      console.log(this.pushid);
      const data = {
        lang:       lang.language,
        pushid:     this.pushid,
      };
      let seq = this.api.post('api/updatelangue', data).share();

      seq.subscribe((res: any) => {
      }, err => {
        console.error('ERROR', err);
      });
      return seq;
    }
  }

}



